import { createMuiTheme } from '@material-ui/core';
import { Breakpoint, BreakpointValues } from '@material-ui/core/styles/createBreakpoints';

export const breakpoints: BreakpointValues = {
  xs: 0,
  smMob: 320,
  lgMob: 375,
  xlMob: 430,
  sm: 600,
  md: 960,
  lg: 1280,
  xl: 1920,
};

export type BreakpointMedia = { [key in Breakpoint]: string };

export const bp: BreakpointMedia = Object.keys(breakpoints).reduce((result, key) => {
  result[key as keyof BreakpointMedia] = `@media (min-width: ${breakpoints[key as keyof BreakpointValues]}px)`;
  return result;
}, {} as BreakpointMedia);

