import { css } from 'emotion';
import { useCallback, useMemo, useState } from 'react'
import { Collapse, Drawer, List, ListItem, ListItemIcon, ListItemText } from '@material-ui/core'
import { ExpandLess, ExpandMore } from '@material-ui/icons';
import getAgencyFeatures from './AgencyFeatures';
import React from 'react';
import { useHistory, useLocation } from 'react-router';
import styled from '@emotion/styled';

const Logo = styled.img`
  height: 45px;
  filter: contrast(6) grayscale(100%);
  position: relative;
  left: 20px;
`;

const SubTitle = styled.span`
  text-transform: lowercase;
  font-size: 12px;
  position: absolute;
  top: 31px;
  left: 145px;
`;

const DrawerStyles = css`
  width: 200px;
  flex-shrink: 0;
`;

const DrawerPaperStyles = css`
  width: 200px;
  
  &:after {
    top: 0;
    right: 0;
    bottom: 0;
    content: '';
    position: absolute;
    border-right: 1px solid;
    border-right-color: rgba(0, 0, 0, 0.12);
  }
`;

const ListStyles = css`
  padding: 10px 0;
`;

const ListItemStyles = css`
  padding: 10px 16px;
`;

const ListItemSelectedStyles = css`
  :after {
    top: 0;
    right: 0;
    bottom: 0;
    content: '';
    z-index: 1;
    position: absolute;
    border-right: 4px solid;
    border-right-color: #000;
  }
`;

const ListItemIconStyles = css`
  min-width: 32px;
  font-size: 16px;
`;

interface SelectedItem {
  firstLevel?: number;
  secondLevel?: number;
}

const Navigation = () => {
  const { pathname } = useLocation();
  const { push } = useHistory();
  const agencyFeatures = useMemo(() => 
    getAgencyFeatures()
  , []);

  const [expandedItems, setExpandedItems] = useState<Number[]>([]);
  const [selectedItem, setSelectedItem] = useState<SelectedItem>({});

  React.useEffect(
    () => {
      const initialExpandedItemIndex = agencyFeatures.findIndex(feature => feature.path === '/' ? pathname === feature.path : pathname.startsWith(feature.path));
      const initialSelectedItemIndex = initialExpandedItemIndex > -1 && agencyFeatures[initialExpandedItemIndex].subFeatures ? 
        agencyFeatures[initialExpandedItemIndex].subFeatures?.findIndex(subFeature => pathname === subFeature.path) : undefined;
      
      setSelectedItem({
        firstLevel: initialExpandedItemIndex,
        secondLevel: initialSelectedItemIndex,
      })

      if (initialExpandedItemIndex > -1 && initialSelectedItemIndex !== undefined && initialSelectedItemIndex > -1 && expandedItems.indexOf(initialExpandedItemIndex) === -1) {
        setExpandedItems([...expandedItems, initialExpandedItemIndex])
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [agencyFeatures, pathname],
  )

  const onSectionToggle = useCallback(
    (sectionIndex: number) => {
      const foundIndex = expandedItems.indexOf(sectionIndex);
      if (!expandedItems.includes(sectionIndex)) {
        setExpandedItems([...expandedItems, sectionIndex]);
      } else {
        setExpandedItems(expandedItems.slice(0, foundIndex).concat(expandedItems.slice(foundIndex + 1)));
      }
    },
    [expandedItems],
  );

  const onItemClick = (path: string, firstLevel?: number, secondLevel?: number) => {
    push(path);
  };

  return (
    <Drawer
      className={DrawerStyles}
      variant="permanent"
      classes={{
        paper: DrawerPaperStyles
      }}
    >
      <div>
        <Logo src="https://static.apos-test.com.au/client-au/logo.png" alt="APositive" /><SubTitle>Noir</SubTitle>
      </div>
      <List disablePadding={true} className={ListStyles}>
        {
          agencyFeatures.map(({ path, icon, label, subFeatures }, i) => 
            <React.Fragment key={i}>
              <ListItem
                button
                classes={{
                  root: ListItemStyles,
                  selected: ListItemSelectedStyles,
                }}
                onClick={() => !subFeatures ? onItemClick(path, i) : onSectionToggle(i)} selected={selectedItem.firstLevel === i && selectedItem.secondLevel === undefined}
              >
                <ListItemIcon className={ListItemIconStyles}>{!!icon && icon}</ListItemIcon><ListItemText primary={label} />
                {
                  !!subFeatures && (
                    expandedItems.includes(i) ? (
                      <ExpandLess />
                    ) : (
                      <ExpandMore />
                    )
                  )
                }
              </ListItem>
              {
                !!subFeatures &&
                <Collapse timeout={100} in={expandedItems.includes(i)}>
                  <List disablePadding={true}>
                    {
                      subFeatures.map(({ path, label }, j) => 
                        <ListItem
                          key={j}
                          button
                          classes={{
                            root: ListItemStyles,
                            selected: ListItemSelectedStyles,
                          }}
                          onClick={() => onItemClick(path, i, j)} selected={selectedItem.firstLevel === i && selectedItem.secondLevel === j}
                        >
                          <ListItemIcon className={ListItemIconStyles} />
                          <ListItemText primary={label} />
                        </ListItem>
                      )
                    }
                  </List>
                </Collapse>
              }
            </React.Fragment>
          )
        }
      </List>
    </Drawer>
  )
}

export default Navigation
